@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/static/pretendard.css");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body,
#__next {
  width: 100%;
  min-height: 100vh;
  font-family: Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto,
    "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Noto Sans KR",
    "Malgun Gothic", sans-serif;
}

/* #__next {
  background: linear-gradient(0deg, #A0AEC0, #EDF2F7);
} */

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

img {
  width: 100%;
}

.active-top {
  transition: 1s ease;
  background: rgba(255, 255, 255, 0.9);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.container {
  max-width: 768px;
  margin: auto;
  overflow: auto;
  padding: 0 10px;
}

.card {
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.card-page {
  padding: 15px 30px;
}

.post-title {
  margin: 10px 0;
}

.post-date {
  background: #f4f4f4;
  margin-bottom: 20px;
  padding: 3px 10px;
}

.post-body {
  margin: 2em 0 2em 0;
}

.post-body ul,
ol,
li {
  font-size: 110%;
  line-height: 2.3;
  font-weight: bold;
  margin: 10px 0;
  list-style-position: inside;
}

.post-body pre {
  background: #f4f4f4;
  border-radius: 16px;
  padding: 20px;
  margin: 20px 0;
  line-height: 2;
  overflow-x: scroll;
}

.title {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.summary {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

@media (min-width: 30em) {
  .posts {
    grid-template-columns: 1fr;
  }

  .summary {
    -webkit-line-clamp: 2;
  }
}

@media (min-width: 48em) {
  .summary {
    -webkit-line-clamp: 2;
  }
}

@media (min-width: 62em) {
  .summary {
    -webkit-line-clamp: 2;
  }
}

